import React from 'react';
// import About from './About';
import Home from './Home';
import Gallery from '../Body/Gallery';
import About_me from './About_me';
import Skill from './Skill';
import CopyaC from '../Body/CopyaC';
import Development from './Development';
import Road_to_dev from './Road_to_dev';
import Error from '../Footer/Error';
import { Switch, Route } from 'react-router-dom';


const Nav = () => {
    return (
        <Switch>
            <Route exact path='/' component={Home} />
            <Route exact path='/about me' component={About_me} />
            <Route exact path='/Copy All Code' component={CopyaC} />
            <Route exact path='/skill' component={Skill} />
            <Route exact path='/gallery' component={Gallery} />
            <Route exact path='/Road to dev' component={Road_to_dev} />
            <Route path='' component={Error} />
        </Switch>
    );

}

export default Nav;