import React from 'react';
import copy from "copy-to-clipboard";
import Allcd from '../Js/Allcd';
import { Button } from '@material-ui/core';

class Code extends React.Component {
    cpyCode = (temp) => {
        Allcd.filter(code => code.id == temp).map(filterCode => (
            <li>
                {copy(filterCode.mCode)}
            </li>
        ));
        alert('Code Copied Succesfully');
    }
    render() {
        return (
            <> {
                Allcd.map((item) => (
                    <div className='oCode fx'>
                        <h2>{item.id} {item.name} Page</h2>
                        <div className='mCode'>
                            <pr>
                                <code>{item.mCode}</code>
                            </pr>
                        </div>
                        <div className='btnCode'>
                            <Button
                              variant='outlined'
                                onClick={() => this.cpyCode(item.id)}
                            >Copy</Button>
                        </div>
                    </div>
                ))
            }

            </>
        );
    }
}
export default Code;