import React from 'react';
import st1 from '../Images/ts/st1.gif';
import Right from '../Footer/Right';
import Rightarr from '@material-ui/icons/ArrowRightAlt';
import '../Css/rtd.css';
const Road_to_dev = () => {
    return (
        <>
            <div className='rdOuter'>
                <div className='Road'>
                    <div className='rdCircle fx'>
                        <h1>Road To Web Developer</h1>
                    </div>
                    <div className='rdInnr fx'>
                        <Rightarr />
                        <Rightarr />
                    </div>
                </div>
                <div className='St fx'>
                    <div className='fSt'>
                        <img src={st1} />
                    </div>
                    <div className='stLeft'>
                        <h2>Student</h2>
                    </div>
                    <div className='stRight'>
                        <h2>Web Developer</h2>
                    </div>
                </div>
            </div>
            <div className='stpDev'>
                <ul>
                    <h2>There are Several Steps to be Web Developer(Programmer)</h2>
                    <li>Start From Basics(Programming)</li>
                    <li>Basics of Web Development</li>
                    <li>Daily Practice on Development</li>
                    <li>Work Frontend and Backend (Advanced) with Database</li>
                    <li>Work with projects</li>
                </ul>
            </div>
            <Right />
        </>
    )
}
export default Road_to_dev;