class CopyaC extends React.Component {

    render() {

        return (
            <>
                <div className='pprCpc'></div>
                <div className='oCpc fx'>
                
                    <div className='imgCpc'>
                        <img src={cpy} />
                    </div>
                </div>
                <div className='pprCpc2'>
                </div>
                <div className='cpcCode'>
                    <Code />
                </div>
            </>
        );
    }

}
export default CopyaC;